// Blog css
.p-blog {
    background-color: #eee;

    &__inner {
        padding-top: 50px;
        @media screen and (max-width: 992px) {
            padding-left: 20px;
            padding-right: 20px;
        }

    }

    &__tab {
        background-color: #ffff;
        display: flex;
        margin-left: -1px;
        padding: 1px;
        @media screen and (max-width: 575px) {
            flex-wrap: wrap;
        }
    }

    &__tablink {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 1px;
        width: 25%;
        height: 50px;
        color: #fff;
        background-color: #666;
        text-transform: uppercase;
        font-size: 1.8rem;
        font-weight: bold;
        font-family: 'Arial';
        cursor: pointer;

        &--active {
            background-color: #f15a24;
        }

        @media screen and (max-width: 575px) {
            width: 100%;
        }
    }

    &__content {
        margin-top: 45px;
        flex-wrap: wrap;
        margin-left: -20px;
        display: flex;

        // &--active {
        //     display: flex;
        // }

        a {
            background-color: #fff;
            margin-left: 20px;
            width: calc(33.33% - 20px);
            margin-bottom: 20px;
            @media screen and (max-width: 767px) {
                width: calc(50% - 20px);
            }
        }
    }

    &__img {
        position: relative;
    }

    &__img {
        height: 240px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__cate {
        color: #ffff;
        font-family: 'Arial';
        font-weight: 400;
        font-size: 1.3rem;
        max-width: 120px;
        width: 100%;
        height: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #f15a24;
        text-transform: uppercase;
        position: absolute;
        top: 0;
        left: 0;
    }

    &__info {
        height: 180px;
        display: flex;
        flex-direction: column;
        padding-top: 25px;
        padding-left: 30px;
        padding-right: 30px;
        padding-bottom: 20px;
        @media screen and (max-width: 767px) {
            height: 150px;
        }
    }

    &__name {
        color: #4d4d4d;
        font-size: 1.5rem;
        font-weight: bold;
        font-family: 'Meiryo';
        @media screen and (max-width: 767px) {
            font-size: 1.4rem;
        }
    }

    &__date {
        color: #808080;
        font-weight: 400;
        font-family: 'Meiryo';
        font-size: 1.3rem;
        border-top: 1px solid #ccc;
        padding-top: 10px;
        margin-top: auto;
        @media screen and (max-width: 767px) {
            font-size: 1.4rem;
        }
    }
}
// Blog css

.p-tc {
    display: none;
}
