/* =========================================================
base - 基本設定
========================================================= */
html {
    font-size: 62.5%;
}

html,
body {
    overflow-x: hidden;
}

body {
    font-family: $font-family-jp-sans-serif;
    color: $base-font-color;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    transition: 0.5s;
    text-decoration: none;

    &:hover {
        opacity: 0.7;
        transition: 0.5s;
    }
}

img {
    max-width: 100%;
    display: block;
}

.l-container {
    max-width: 1000px;
    margin: 0 auto;
}

@include mq(lg, max) {
    img {
        max-width: 100%;
    }
}

@include mq(lg, min) {
    .sp-only {
        display: none;
    }
}

@include mq(lg, max) {
    .pc-only {
        display: none;
    }
}
